import express from 'express';
import expressJWT from 'express-jwt';
import { Joi, celebrate } from 'celebrate';
import getUserReports from '../controllers/users_get_reports';
import { baseUrl } from '../baseurl';
import env from '../../../config/env';
import { getIsRevokedCallBackForAccessToken } from '../../../helpers/revokeToken/checkRevokedToken';
import getLoggedInUser from '../../../middlewares/get_loggedIn_user';

const router = express.Router();

const userLocationSchema = {
  query: {
    divisionId: Joi.string(),
    videoId: Joi.string().required(),
  },
};

/**
 * @swagger
 * /users/reports:
 *   get:
 *     tags:
 *       - Users
 *     description: It returns the list of users.
 *     consumes:
 *       - application/json
 *     parameters:
 *      - in: "query"
 *        name: divisionId
 *        description: "It takes a division id to pass the list of users"
 *        required: true
 *        schema:
 *           type: object
 *           properties:
 *              divisionId :
 *                  type: string
 *      - in: "query"
 *        name: role
 *        description: "It takes a role id to pass the list of users having that role"
 *        required: true
 *        schema:
 *           type: object
 *           properties:
 *              role :
 *                  type: string
 *      - in: "query"
 *        name: "limit"
 *        description: "Sets the number of users to be returned"
 *        schema:
 *          type: object
 *          properties:
 *             limit:
 *                 type: number
 *      - in: "query"
 *        name: "offset"
 *        description: "Sets the offset to the list of users"
 *        schema:
 *          type: object
 *          properties:
 *             offset:
 *                 type: number
 *      - in: "query"
 *        name: "userType"
 *        description: "userType can be one of['vv,wv']"
 *        schema:
 *          type: object
 *          properties:
 *             userType:
 *                 type: string
 *      - in: "query"
 *        name: "all"
 *        description: A boolean value to include disabled users
 *        schema:
 *          type: object
 *          properties:
 *             all:
 *                 type: boolean
 *     produces:
 *       - application/json
 *     responses:
 *       200:
 *         description: A JSON object
 *         schema:
 *           type: object
 *           properties:
 *              userId:
 *                  type: string
 *              firstName:
 *                  type: string
 *              lastName:
 *                  type: string
 *              mobileNumber:
 *                  type: string
 *              email:
 *                  type: string
 *       400:
 *         description: Returns 400 if userId or password are invalid
 *         schema:
 *           type: object
 *           properties:
 *              statusCode:
 *                  type: number
 *                  example: 400
 *              error:
 *                  type: string
 *              message:
 *                  type: string
 */

router.get(
  `${baseUrl}/reports`,
  expressJWT({
    secret: env.JWT_SECRET,
    isRevoked: getIsRevokedCallBackForAccessToken(),
  }),
  getLoggedInUser,
  celebrate(userLocationSchema),
  getUserReports
);

export default router;
